.modal {
  display: grid;
  width: 60%;
  height: 60%;
  place-items: end center;
  position: fixed;
  z-index: 90;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  overflow: auto;

  z-index:10;
}

@media screen and (max-width: 480px) {
  .modal {
    display: grid;
    width: 70%;
    height: 60%;
    max-width: 320px;
    max-height: 400px;
    place-items: end center;
    position: fixed;
    z-index: 90;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
    overflow: auto;
  }
}

.all {
  display: grid;
  place-items: end center;
  width: 90%;
  height: 95%;
}

.modal_title {
  display: grid;
  justify-content: center;

  color: #333;
  font-family: Pretendard;
  font-size: 28px;
  font-style: normal;
  font-weight: 600;
  line-height: 40px;
  /* 142.857% */
}

.modal_hr {
  width: 87.5%;
  height: 1px;
  background: #666;
}

.modal_1_close {
  position: fixed;
  width: 200px;
  height: 80px;
  line-height: 50px;
  text-align: center;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
  z-index:110;
}

.btn {
  background-color: transparent;
  border: none;
  outline: none;
  cursor: pointer;

  color: #000;
  font-family: Pretendard;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 16px;
  /* 133.333% */
  text-decoration-line: underline;
}

.contents {
  display: flex;
  justify-content: center;
  align-items: center;

  color: #000;
  font-family: Pretendard;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 22px;
  /* 157.143% */
}


.box {

  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: repeat(2, 2fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 4px;

  flex-shrink: 0;
  border-radius: 4px;
  background: #D6D6D6;
}

.btn_page {
  display: flex;
  justify-content: center;
  flex-direction: row;
}

